import React, { Component } from 'react';

class Project extends Component {
    state={
        showInfos:false
    }

    handleInfo=()=>{
        this.setState({
            showInfo:!this.state.showInfo
        })
    }
    
    render() {
        // let {name, organisme, languagesIcons, source, info, picture, structure}=this.props.item;
        let {name, languagesIcons, source, info, picture, organisme, structure}=this.props.item;

        return (
            <div className="project">
                <h3>{name}</h3>
                <h4>{organisme}</h4>
                <img src={picture} alt="" onClick={this.handleInfo}/>
                <span className="infos"onClick={this.handleInfo}>
                    <img src={languagesIcons} alt=""/> 
                    <i className="fas fa-plus-circle"></i> 
                    
                </span>
            
            {
                this.state.showInfo&&(
                    <div className="showInfos">
                        <div className="infosContent">
                            <div className="head">
                                <h2>{name}</h2>
                                <div className="sourceCode">
                                    <a href={source} rel="noopener noreferrer" className="button" target="_blank">Site de l'{structure}</a></div>
                                </div>
                                <p className="text">{info}</p>

                                <div className="button return" onClick={this.handleInfo}>Retourner sur la page </div>
                        </div>
                    </div>


                )
            }
            </div>
        );
    }
}

export default Project;