import React, { Component } from 'react';
import ImageSlider from './ImageSlider';
import ImageSlider2 from './ImageSlider2';
import { sliderData } from '../../../data/sliderData';
import { sliderData2 } from '../../../data/sliderData2';


class Hobbies extends Component {
    state={
        showInfo1s:false
    }
    state={
        showInfo2s:false
    }


    handleInfo1=()=>{
        this.setState({
            showInfo1:!this.state.showInfo1
        })
    }
    handleInfo2=()=>{
        this.setState({
            showInfo2:!this.state.showInfo2
        })
    }
    render() {

        return (
        <div className="hobbies">
            <h3>Int√©r√™ts</h3>
            <ul>
                <li className="hobby">

                    <i className="fas fa-tshirt"></i><span>Couture</span>
                    <span className='infos' onClick={this.handleInfo1}>
                    <i className="fas fa-plus-circle"></i>D√©couvrir mes cr√©ations vestimentaires</span>
                     
                   </li>  
               
                <li className="hobby">

                    <i className="fas fa-carrot"></i><span>Cuisine</span>
                    <span className='infos' onClick={this.handleInfo2}>
                    <i className="fas fa-plus-circle"></i>D√©couvrir mes cr√©ations culinaires</span>
 
                    </li> 

                <li className="hobby">
                    <i className="fas fa-horse riding"></i>
                    <span>Equitation</span>
                    {/* <span className="infos"onClick={this.handleInfo}>
                     <i className="fas fa-plus-circle"></i> 
                     </span> */}
                </li>
            </ul>
            
        

    {
    this.state.showInfo1&&(
        <div className="showInfos">
            <div className="infosContent">
                    <ImageSlider slides={sliderData} className="imageSlider"/>    
                <div className="button return" onClick={this.handleInfo1}>Retourner sur la page </div>
            </div>
        </div>  
        )
    }
    {
    this.state.showInfo2&&(
        <div className="showInfos">
            <div className="infosContent">
                    <ImageSlider2 slides={sliderData2}className="imageSlider2"/>    
                <div className="button return" onClick={this.handleInfo2}>Retourner sur la page </div>
            </div>
        </div>  
        )
    }
    </div>
);
}
}



export default Hobbies;











