import React, { Component } from 'react';
import ProgressBar from './ProgressBar';

class Logiciels extends Component {
    state = {
    logiciels :[
        {id:1, value: "Catia v5", xp:4},
        {id:2, value: "Solidworks", xp:1.5},
        {id:3, value: "Ansys", xp:2},
    ],
    }
    render() {
        let {logiciels}=this.state;

        return (
            <div className="logicielsLanguages">
                <ProgressBar
                logiciels={logiciels}
                className="logicielsDisplay"
                title="logiciels"
                />
                <div className="langues">
                <h3>langues</h3>
                <li>Anglais : TOEIC (900)</li>
                <li>Allemand : Goethe Zertifikat B2 </li>
                </div>
                
            </div>
        );
    
    }
}

export default Logiciels;